import React from 'react'
import PropTypes from 'prop-types'
import { isEmpty } from 'lodash'
import { DANG_LAM, HOAN_THANH, DA_HUY } from 'constants/Constants';
import { Tooltip } from 'reactstrap';
import { DA_GIAO } from '../../../../../constants/Constants';

class CheckboxFilter extends React.Component {
  state = {
    dangLamTooltip: false,
    hoanThanhTooltip: false,
    daGiaoTooltip: false,
    daHuyTooltip: false,
  }

  toggleDangLamTooltip = () => { this.setState({ dangLamTooltip: !this.state.dangLamTooltip }); }
  toggleHoanThanhTooltip = () => { this.setState({ hoanThanhTooltip: !this.state.hoanThanhTooltip }); }
  toggleDaGiaoTooltip = () => { this.setState({ daGiaoTooltip: !this.state.daGiaoTooltip }); }
  toggleDaHuyTooltip = () => { this.setState({ daHuyTooltip: !this.state.daHuyTooltip }); }


  filter = (event) => {
    this.checkCheckedLocalStorage();
    if (this.refs.dangLam.checked && this.refs.hoanThanh.checked && this.refs.daHuy.checked && this.refs.daGiao.checked) {
      this.props.filterHandler();
    } else {
      this.props.filterHandler({ callback: this.isFiltered });
    }
  }

  isFiltered = (targetValue) => {
    if (targetValue === DANG_LAM) {
      return (this.refs.dangLam.checked);
    } else if (targetValue === HOAN_THANH) {
      return (this.refs.hoanThanh.checked);
    } else if (targetValue === DA_GIAO) {
      return (this.refs.daGiao.checked);
    } else if (targetValue === DA_HUY) {
      return (this.refs.daHuy.checked);
    }
  }

  checkCheckedLocalStorage = () => {
    if (this.refs.dangLam.checked) { localStorage.removeItem('isDangLam'); }
    else { localStorage.isDangLam = 'khong chon' }

    if (this.refs.hoanThanh.checked) { localStorage.removeItem('isHoanThanh'); }
    else { localStorage.isHoanThanh = 'khong chon' }

    if (this.refs.daGiao.checked) { localStorage.removeItem('isDaGiao'); }
    else { localStorage.isDaGiao = 'khong chon' }

    if (this.refs.daHuy.checked) { localStorage.removeItem('isDaHuy'); }
    else { localStorage.isDaHuy = 'khong chon' }
  }

  cleanFiltered = () => {
    this.refs.okCheckbox.checked = true;
    this.refs.nokCheckbox.checked = true;
    this.props.filterHandler();
  }

  componentDidMount() {
    const isDangLam = isEmpty(localStorage.isDangLam);
    const isHoanThanh = isEmpty(localStorage.isHoanThanh);
    const isDaGiao = isEmpty(localStorage.isDaGiao);
    const isDaHuy = isEmpty(localStorage.isDaHuy);
    this.refs.dangLam.checked = isDangLam;
    this.refs.hoanThanh.checked = isHoanThanh;
    this.refs.daGiao.checked = isDaGiao;
    this.refs.daHuy.checked = isDaHuy;
    this.filter();
  }

  render() {
    return (
      <div>
        <input
          id="dangLamInput"
          ref='dangLam'
          type='checkbox'
          className='filter'
          onChange={this.filter}
          defaultChecked={true}
        />
        <label id="dangLam" className="text-body" htmlFor="dangLamInput">{this.props.dangLam}</label>
        <Tooltip placement="bottom" isOpen={this.state.dangLamTooltip} autohide={false} target="dangLam" toggle={this.toggleDangLamTooltip}>{DANG_LAM}</Tooltip>
        
        <input
          id="hoanhThanhInput"
          ref='hoanThanh'
          type='checkbox'
          className='filter'
          onChange={this.filter}
          defaultChecked={true}
          style={{ marginLeft: 5 + 'px' }}
        />
        <label id="hoanThanh" className="text-success" htmlFor="hoanhThanhInput">{this.props.hoanThanh}</label>
        <Tooltip placement="bottom" isOpen={this.state.hoanThanhTooltip} autohide={false} target="hoanThanh" toggle={this.toggleHoanThanhTooltip}>{HOAN_THANH}</Tooltip>
        
        <input
          id="daGiaoInput"
          ref='daGiao'
          type='checkbox'
          className='filter'
          onChange={this.filter}
          defaultChecked={true}
          style={{ marginLeft: 5 + 'px' }}
        />
        <label id="daGiao" className="text-warning" htmlFor="daGiaoInput">{this.props.daGiao}</label>
        <Tooltip placement="bottom" isOpen={this.state.daGiaoTooltip} autohide={false} target="daGiao" toggle={this.toggleDaGiaoTooltip}>{DA_GIAO}</Tooltip>
        
        <input
          id="daHuyInput"
          ref='daHuy'
          type='checkbox'
          className='filter'
          onChange={this.filter}
          defaultChecked={true}
          style={{ marginLeft: 5 + 'px' }}
        />
        <label id="daHuy" className="text-danger" htmlFor="daHuyInput">{this.props.daHuy}</label>
        <Tooltip placement="bottom" isOpen={this.state.daHuyTooltip} autohide={false} target="daHuy" toggle={this.toggleDaHuyTooltip}>{DA_HUY}</Tooltip>
      </div>
    );
  }
}

CheckboxFilter.propTypes = {
  filterHandler: PropTypes.func.isRequired,
  dangLam: PropTypes.string,
  hoanThanh: PropTypes.string,
  daGiao: PropTypes.string,
  daHuy: PropTypes.string,
};

CheckboxFilter.defaultProps = {
  dangLam: 'DL',
  hoanThanh: 'HT',
  daGiao: 'DG',
  daHuy: 'DH',
};

export default CheckboxFilter;